@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "Lucida Console", "Andale Mono", monospace;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    max-width: 770px;
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.btn {
  position: relative;
  display: inline-flex;
  font-family: "Roboto Condensed", "Arial Narrow", sans-serif;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.2s ease-out;
}
@media (hover: hover) {
  .btn:hover {
    opacity: 0.6;
  }
}
.btn.focus-visible:focus:not(:active) {
  outline: 3px dashed rgba(0, 178, 255, 0.6);
}
.btn.focus-visible:active {
  opacity: 0.2;
}
.btn:disabled, .btn.disabled {
  opacity: 0.6;
  pointer-events: none;
}

.btn--add {
  position: relative;
}
.btn--add::after {
  position: absolute;
  z-index: -1;
  top: 10px;
  left: 10px;
  width: 100%;
  height: 100%;
  border-radius: 3px;
  outline: 2px solid #f2f5f6;
  opacity: 0;
  transition: opacity 0.2s ease-out;
  content: "";
}
@media (hover: hover) {
  .btn--add:hover {
    opacity: 1;
  }
  .btn--add:hover::after {
    opacity: 1;
  }
}
.btn--add.focus-visible:focus:not(:active) {
  outline: 3px dashed rgba(0, 178, 255, 0.6);
}
.btn--add.focus-visible:active {
  opacity: 1;
}
.btn--add.focus-visible:active::after {
  opacity: 0.2;
}
.btn--add:disabled, .btn--add.disabled {
  color: lightgrey;
  pointer-events: none;
}

.title {
  font-family: "Roboto Condensed", "Arial Narrow", sans-serif;
  font-weight: 400;
}

.header {
  position: sticky;
  top: 0;
  z-index: 5;
  min-height: 120px;
  padding-top: 30px;
  padding-bottom: 35px;
  color: #f2f5f6;
  background-image: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (max-width: 1023px) {
  .header {
    position: static;
    min-height: 100px;
    padding-top: 20px;
    padding-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 70px;
    padding-top: 15px;
    padding-bottom: 20px;
  }
}

.header__container {
  display: grid;
  grid-template-columns: minmax(1fr, 375px) minmax(1fr, 625px) minmax(175px, 238px);
  gap: 10px 0;
  align-items: center;
}
@media (max-width: 1023px) {
  .header__container {
    display: flex;
    justify-content: space-between;
  }
}

.header__logo {
  grid-column: 1/2;
}
@media (max-width: 1023px) {
  .header__logo {
    flex-grow: 0;
    flex-shrink: 0;
    width: 215px;
    height: auto;
    margin-right: 0;
  }
}
@media (max-width: 767px) {
  .header__logo {
    width: 35px;
  }
}

.header__image {
  margin-right: 15px;
}

.header__contacts {
  grid-column: 2/3;
  max-width: 480px;
}

.header__btn {
  grid-column: 3/4;
  justify-self: start;
  min-width: 160px;
  min-height: 40px;
  margin-left: 15px;
  padding: 11px 15px;
  font-size: 14px;
  line-height: 16px;
  color: #f2f5f6;
  border: 1px solid #f2f5f6;
}
@media (max-width: 1023px) {
  .header__btn {
    display: none;
  }
}

.logo {
  max-width: 100%;
  height: auto;
  object-fit: contain;
  transition: opacity 0.2s ease-out;
}
@media (hover: hover) {
  .logo:hover {
    opacity: 0.6;
  }
}
.logo.focus-visible:focus:not(:active) {
  outline: 3px dashed rgba(0, 178, 255, 0.6);
}
.logo.focus-visible:active {
  opacity: 0.2;
}

.contacts {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px 10px;
}
@media (max-width: 1023px) {
  .contacts {
    display: flex;
    margin-right: 20px;
    align-self: start;
  }
}
@media (max-width: 767px) {
  .contacts {
    margin-right: 9px;
  }
}

.contacts__link {
  display: flex;
  align-items: center;
  font-style: normal;
  color: inherit;
  transition: opacity 0.2s ease-out;
}
@media (max-width: 1023px) {
  .contacts__link {
    width: 40px;
    height: 40px;
    padding: 10px;
  }
}
@media (hover: hover) {
  .contacts__link:hover {
    opacity: 0.6;
  }
}
.contacts__link.focus-visible:focus:not(:active) {
  outline: 3px dashed rgba(0, 178, 255, 0.6);
}
.contacts__link.focus-visible:active {
  opacity: 0.2;
}

.contacts__icon {
  flex-shrink: 0;
  color: #00b2ff;
}

.contacts__txt {
  margin-left: 15px;
}
@media (max-width: 1023px) {
  .contacts__txt {
    display: none;
  }
}

.footer {
  margin-top: auto;
  padding-top: 50px;
  padding-bottom: 55px;
  font-size: 16px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.7);
  background-color: #041827;
}
@media (max-width: 1023px) {
  .footer {
    padding-bottom: 65px;
  }
}
@media (max-width: 767px) {
  .footer {
    padding-top: 32px;
    padding-bottom: 35px;
    overflow: hidden;
  }
}

.footer__container {
  display: grid;
  grid-template-columns: 1fr 69.26%;
  gap: 30px;
}
@media (max-width: 1023px) {
  .footer__container {
    grid-template-columns: 1fr;
    gap: 35px 110px;
  }
}
@media (max-width: 767px) {
  .footer__container {
    grid-template-columns: 1fr;
    gap: 25px;
  }
}

.footer__logo-wrapper {
  display: flex;
  flex-direction: column;
}
@media (max-width: 1023px) {
  .footer__logo-wrapper {
    flex-direction: row;
    justify-content: space-between;
    grid-column: 1/-1;
  }
}
@media (max-width: 767px) {
  .footer__logo-wrapper {
    flex-direction: column;
    align-items: center;
  }
}

.footer__logo {
  display: block;
  width: 215px;
  margin-bottom: 30px;
}
@media (max-width: 1023px) {
  .footer__logo {
    margin-bottom: 0;
  }
}

.footer__social-list {
  margin: 0;
  margin-left: 10px;
}
@media (max-width: 1023px) {
  .footer__social-list {
    margin-top: 5px;
  }
}
@media (max-width: 767px) {
  .footer__social-list {
    margin-top: 15px;
  }
}

.footer__accordion {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .footer__accordion {
    flex-direction: column;
    justify-content: flex-start;
  }
}

.footer__column {
  display: flex;
  flex-direction: column;
  align-self: stretch;
  width: 55.62%;
}
@media (max-width: 1023px) {
  .footer__column {
    width: 32.84%;
  }
}
@media (max-width: 767px) {
  .footer__column {
    width: auto;
    margin-left: -100px;
    margin-right: -100px;
    padding: 5px 100px;
    border: 1px solid rgba(255, 255, 255, 0.7);
  }
}

.footer__column + .footer__column {
  width: 40.83%;
}
@media (max-width: 1023px) {
  .footer__column + .footer__column {
    width: 47.76%;
  }
}
@media (max-width: 767px) {
  .footer__column + .footer__column {
    width: auto;
    margin-top: -1px;
    border-top: none;
  }
}

.footer__title {
  margin-top: 5px;
  margin-bottom: 20px;
  font-size: 15px;
  line-height: 20px;
  letter-spacing: 0.05em;
  color: #ffffff;
  text-transform: uppercase;
}
@media (max-width: 1023px) {
  .footer__title {
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .footer__title {
    position: relative;
    margin-bottom: 0;
    padding: 15px 0;
  }
  .footer__title::before, .footer__title::after {
    position: absolute;
    top: 22px;
    right: 0;
    width: 15px;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.7);
    content: "";
  }
  .footer__title::after {
    transform: scaleY(1) rotate(90deg);
    will-change: transform;
    transition: transform 0.2s ease-out;
  }
}

@media (max-width: 767px) {
  .is-active .footer__title::after {
    transform: scaleY(0) rotate(90deg);
  }
}

.social-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: fit-content;
  padding: 0;
  list-style-type: none;
}

.social-list__item + .social-list__item {
  margin-left: 15px;
}

.social-list__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  color: #f8fdff;
  transition: opacity 0.2s ease-out;
}
@media (hover: hover) {
  .social-list__link:hover {
    opacity: 0.6;
  }
}
.social-list__link.focus-visible:focus:not(:active) {
  outline: 3px dashed rgba(0, 178, 255, 0.6);
}
.social-list__link.focus-visible:active {
  opacity: 0.2;
}
.social-list__link:disabled, .social-list__link.disabled {
  opacity: 0.6;
  pointer-events: none;
}

.footer-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(4, min-content);
  grid-auto-flow: column;
  gap: 10px 30px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.footer-list--add {
  grid-template-columns: 1fr;
  grid-template-rows: unset;
  grid-auto-flow: unset;
  gap: 10px;
}
@media (max-width: 1023px) {
  .footer-list {
    grid-template-columns: 1fr;
    grid-template-rows: unset;
    grid-auto-flow: unset;
    gap: 10px;
  }
}
@media (max-width: 767px) {
  .footer-list {
    grid-template-columns: 1fr;
  }
}

.footer-list__item {
  height: min-content;
}
.footer-list__item:first-child {
  margin-top: 0;
}
@media (max-width: 767px) {
  .footer-list__item:last-child {
    margin-bottom: 15px;
  }
}

.footer-list--add .footer-list__item:first-child {
  margin: 0;
  font-style: normal;
}
@media (max-width: 1023px) {
  .footer-list--add .footer-list__item:first-child {
    margin-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .footer-list--add .footer-list__item:first-child {
    margin-bottom: 5px;
  }
}

.footer-list__link {
  display: block;
  font-style: normal;
  color: inherit;
  transition: opacity 0.2s ease-out;
}
@media (hover: hover) {
  .footer-list__link:hover {
    opacity: 0.6;
  }
}
.footer-list__link.focus-visible:focus:not(:active) {
  outline: 3px dashed rgba(0, 178, 255, 0.6);
}
.footer-list__link.focus-visible:active {
  opacity: 0.2;
}
.footer-list__link:disabled, .footer-list__link.disabled {
  opacity: 0.6;
  pointer-events: none;
}

.main {
  background-color: #f8fdff;
}

.hero {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: -120px;
  padding-top: 120px;
  padding-bottom: 120px;
  min-height: 880px;
  color: #f2f5f6;
  background-color: #3f5b77;
}
.hero::before {
  position: absolute;
  z-index: 2;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%);
  content: "";
}
@media (max-width: 1023px) {
