  background-color: #3f5b77;
}
@media (max-width: 1023px) {
  .services__item {
    min-height: 250px;
  }
}

.services__link {
  display: block;
  height: 100%;
  padding: 50px 60px 60px 40px;
  font-family: "Roboto Condensed", "Arial Narrow", sans-serif;
  font-size: 20px;
  line-height: 25px;
  color: inherit;
}
.services__link::before {
  position: absolute;
  z-index: 1;
  display: block;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #041827;
  opacity: 0.5;
  transition: opacity 0.2s ease-out, background-color 0.2s ease-out;
  content: "";
}
.services__link::after {
  position: absolute;
  z-index: 3;
  bottom: 0;
  right: 0;
  width: 60px;
  height: 60px;
  background-color: #00b2ff;
  background-image: url("data:image/svg+xml,%3Csvg width='5' height='10' viewBox='0 0 5 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.476196 8.80957L4.28572 5.00005L0.476196 1.19052' stroke='white' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 5px 10px;
  border-radius: 3px 0px 0px 0px;
  opacity: 1;
  transition: opacity 0.2s ease-out;
  content: "";
}
@media (max-width: 1023px) {
  .services__link {
    padding: 40px 60px 60px 45px;
  }
}
@media (max-width: 767px) {
  .services__link {
    padding: 30px 40px 40px 30px;
    font-size: 18px;
    line-height: 25px;
  }
  .services__link::after {
    width: 40px;
    height: 40px;
    background-size: 4px 6px;
  }
}
@media (min-width: 767px) {
  .services__link:hover::before {
    background-color: #00b2ff;
    opacity: 0.8;
  }
  .services__link:hover::after {
    opacity: 0;
  }
}
.services__link.focus-visible:focus:not(:active) {
  outline: 3px dashed rgba(0, 178, 255, 0.6);
}
.services__link.focus-visible:active::before {
  background-color: #00b2ff;
  opacity: 1;
}

.services__background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.services__txt {
  position: relative;
  z-index: 3;
  max-width: 170px;
  margin: 0;
}
@media (max-width: 1023px) {
  .services__txt {
    max-width: 150px;
  }
}
@media (max-width: 767px) {
  .services__txt {
    max-width: 135px;
  }
}

